import { exists } from "../runtime";

/**
 *
 * @export
 * @interface Service
 */
export interface Service {
  /**
   *
   * @type {string}
   * @memberof Service
   */
  id?: string;
  /**
   *
   * @type {string}
   * @memberof Service
   */
  name?: string;
  /**
   *
   * @type {string}
   * @memberof Service
   */
  description?: string;
  /**
   *
   * @type {string}
   * @memberof Service
   */
  base_url?: string;
  /**
   *
   * @type {string}
   * @memberof Service
   */
  summary?: string;
  /**
   *
   * @type {string}
   * @memberof Service
   */
  assets?: any[];
  /**
   *
   * @type {string}
   * @memberof Service
   */
  auth_tokens?: string[];
  /**
   *
   * @type {string}
   * @memberof Service
   */
  enabled?: true;
  /**
   *
   * @type {string}
   * @memberof Service
   */
  tags?: string[];
  /**
   *
   * @type {string}
   * @memberof Service
   */
  public_keys?: any[];
}

export function ServiceFromJSON(json: any): Service {
  return ServiceFromJSONTyped(json, false);
}

export function ServiceFromJSONTyped(
  json: any,
  ignoreDiscriminator: boolean
): Service {
  if (json === undefined || json === null) {
    return json;
  }
  return {
    id: !exists(json, "id") ? undefined : json["id"],
    name: !exists(json, "name") ? undefined : json["name"],
    description: !exists(json, "description") ? undefined : json["description"],
    base_url: !exists(json, "base_url") ? undefined : json["base_url"],
    summary: !exists(json, "summary") ? undefined : json["summary"],
    assets: !exists(json, "assets") ? undefined : json["assets"],
    auth_tokens: !exists(json, "auth_tokens") ? undefined : json["auth_tokens"],
    enabled: !exists(json, "enabled") ? undefined : json["enabled"],
    tags: !exists(json, "tags") ? undefined : json["tags"],
    public_keys: !exists(json, "public_keys") ? undefined : json["public_keys"],
  };
}

export function ServiceToJSON(value?: Service | null): any {
  if (value === undefined) {
    return undefined;
  }
  if (value === null) {
    return null;
  }
  return {
    id: value.id,
    name: value.name,
    description: value.description,
    base_url: value.base_url,
    summary: value.summary,
    assets: value.assets,
    auth_tokens: value.auth_tokens,
    enabled: value.enabled,
    tags: value.tags,
    public_keys: value.public_keys,
  };
}
