import { makeStyles } from "@material-ui/styles";
import { Button, CircularProgress, Paper, TextField } from "@material-ui/core";
import { useState } from "react";
import { useAuthContext } from "../../hooks/UseAuth";
import useInput from "../../hooks/UseInput";
import { t } from "../../services/LocaleService";

export interface ServiceInfoProps {
  service: any; // TODO: any
  setService: (data: any | undefined) => void;
}

/**
 * Editable part of the service data.
 */
export default function ServiceInfo({ service, setService }: ServiceInfoProps) {
  const { api } = useAuthContext();
  const [status, setStatus] = useState<"idle" | "pending">("idle");

  const {
    value: serviceName,
    reset: resetServiceName,
    bind: bindServiceName,
  } = useInput(service.name);
  const {
    value: serviceUrl,
    reset: resetUrl,
    bind: bindUrl,
  } = useInput(service.base_url);
  const {
    value: servieDescription,
    reset: resetDescription,
    bind: bindDescription,
  } = useInput(service.description);
  const {
    value: serviceSummary,
    reset: resetSummary,
    bind: bindSummary,
  } = useInput(service.summary);
  const {
    value: servicePublicKey,
    reset: resetPublicKey,
    bind: bindPublicKey,
  } = useInput(service.public_keys[0].pem);

  const handleSubmit = (e: any) => {
    e.preventDefault();

    setStatus("pending"); // set pending status to display circle
    api
      .updateService({ body: { url: serviceUrl, name: serviceName } })
      .then((r) => setService(r))
      .then(() => setStatus("idle")) // todo maybe show some modal with OK
      .catch((e) => {
        console.log(e); // todo better error handling would be nice
        setService(undefined);
        setStatus("idle");
      });
  };

  const handleReset = (e: any) => {
    e.preventDefault();
    resetServiceName();
    resetUrl();
    resetSummary();
    resetDescription();
    resetPublicKey();
  };

  const dataNotChanged =
    serviceName === service.name &&
    serviceUrl === service.base_url &&
    servieDescription === service.description &&
    serviceSummary === service.summary &&
    servicePublicKey === service.public_keys[0].pem;
  const statusPending = status === "pending";
  const classes = useStyles();
  return (
    <Paper elevation={4} className={classes.paper}>
      <div className={classes.info}>
        <div className={classes.heading}>{t("SERVICE.TITLE")}</div>

        <form className={classes.info} noValidate autoComplete="off">
          <TextField
            id="id"
            label={t("SERVICE.ID_LABEL")}
            disabled
            helperText={t("SERVICE.ID_HELPER_TEXT")}
            value={service.id}
          />
          <TextField
            id="serviceName"
            label={t("SERVICE.NAME_LABEL")}
            disabled={statusPending}
            helperText={t("SERVICE.NAME_HELPER_TEXT")}
            {...bindServiceName}
          />
          <TextField
            id="base_url"
            label={t("SERVICE.URL_LABEL")}
            disabled={statusPending}
            helperText={t("SERVICE.URL_HELPER_TEXT")}
            {...bindUrl}
          />
          <TextField
            id="description"
            label={t("SERVICE.DESCRIPTION_LABEL")}
            disabled={statusPending}
            helperText={t("SERVICE.DESCRIPTION_HELPER_TEXT")}
            multiline={true}
            rows={3}
            {...bindDescription}
          />
          <TextField
            id="summary"
            label={t("SERVICE.SUMMARY_LABEL")}
            disabled={statusPending}
            helperText={t("SERVICE.SUMMARY_HELPER_TEXT")}
            {...bindSummary}
          />
          <TextField
            id="public_key"
            label={t("SERVICE.KEY_LABEL")}
            disabled={statusPending}
            helperText={t("SERVICE.KEY_HELPER_TEXT")}
            multiline={true}
            rows={3}
            {...bindPublicKey}
          />
          <div className={classes.buttons}>
            <Button
              variant="contained"
              type="submit"
              onClick={handleReset}
              color="secondary"
              disabled={statusPending || dataNotChanged}
            >
              <span>{t("COMMON.ACTION_RESET")}</span>
            </Button>

            <Button
              variant="contained"
              type="submit"
              onClick={handleSubmit}
              disabled={statusPending || dataNotChanged}
            >
              {statusPending ? (
                <CircularProgress size={"1.5rem"} />
              ) : (
                <span>{t("COMMON.ACTION_UPDATE")}</span>
              )}
            </Button>
          </div>
        </form>
      </div>
    </Paper>
  );
}

const useStyles = makeStyles(() => ({
  paper: {
    margin: "10px",
  },
  info: {
    display: "flex",
    flexFlow: "column",
    margin: "5px",
    "& > div": {
      margin: "20px",
    },
  },
  buttons: {
    display: "flex",
    justifyContent: "space-evenly",
    "& > button": {
      width: "19ch",
    },
  },
  heading: {
    fontWeight: "bold",
  },
}));
