import { useRequireAuth } from "../../../hooks/UseAuth";
import { makeStyles } from "@material-ui/styles";
import { useState } from "react";
import { Button, CircularProgress, Paper, TextField } from "@material-ui/core";
import ImageUpload from "../components/ImageUpload";
import useInput from "../../../hooks/UseInput";
import useRouter from "../../../hooks/UseRouter";
import { routes } from "../../../modules/Routing";
import { t } from "../../../services/LocaleService";

// TODO: hardcoded tags?
const defaultTag = "tutorial";

/**
 * Form which allows user to register a service.
 */
export function ServiceCreation() {
  const { api } = useRequireAuth();
  const router = useRouter();

  const [status, setStatus] = useState<"idle" | "pending">("idle");

  const { value: name, bind: bindName } = useInput("");
  const { value: base_url, bind: bindUrl } = useInput("");
  // TODO add support for the avatar
  const { value: summary, bind: bindSummary } = useInput("");
  const { value: description, bind: bindDescription } = useInput("");
  const { value: public_key, bind: bindPublicKey } = useInput("");

  const handleSubmit = (e: any) => {
    e.preventDefault();

    setStatus("pending"); // set pending status to display circle
    api
      .createNewService({
        // TODO: hardcoded tags?
        body: {
          name,
          base_url,
          summary,
          description,
          public_key,
          tags: [defaultTag],
        },
      })
      .then(() => router.push(routes.home))
      .catch((e) => {
        setStatus("idle");
      });
  };

  const classes = useStyles();
  const pendingStatus = status === "pending";
  return (
    <Paper elevation={4} className={classes.paper}>
      <div className={classes.info}>
        <div className={classes.heading}>{t("SERVICE_FORM.TITLE")}</div>
        {/* TODO maybe add some explanation what is going on */}
        <form className={classes.info} noValidate autoComplete="off">
          <TextField
            id="name"
            label={t("SERVICE.NAME_LABEL")}
            disabled={pendingStatus}
            helperText={t("SERVICE.NAME_HELPER_TEXT")}
            {...bindName}
          />
          <TextField
            id="base_url"
            label={t("SERVICE.URL_LABEL")}
            disabled={pendingStatus}
            helperText={t("SERVICE.URL_HELPER_TEXT")}
            {...bindUrl}
          />
          <TextField
            id="description"
            label={t("SERVICE.DESCRIPTION_LABEL")}
            multiline
            disabled={pendingStatus}
            helperText={t("SERVICE.DESCRIPTION_HELPER_TEXT")}
            {...bindDescription}
          />
          <TextField
            id="summary"
            label={t("SERVICE.SUMMARY_LABEL")}
            multiline
            disabled={pendingStatus}
            helperText={t("SERVICE.SUMMARY_HELPER_TEXT")}
            {...bindSummary}
          />
          {/* TODO: tooltip? */}
          <TextField
            id="public_key"
            label={t("SERVICE.KEY_LABEL")}
            multiline
            disabled={pendingStatus}
            helperText={t("SERVICE.KEY_HELPER_TEXT")}
            {...bindPublicKey}
          />

          <ImageUpload />
          <div className={classes.buttons}>
            <Button
              variant="contained"
              type="submit"
              onClick={handleSubmit}
              disabled={pendingStatus || !name}
            >
              {pendingStatus ? (
                <CircularProgress size={"1.5rem"} />
              ) : (
                <>{t("COMMON.ACTION_CREATE")}</>
              )}
            </Button>
          </div>
        </form>
      </div>
    </Paper>
  );
}

const useStyles = makeStyles(() => ({
  paper: {
    margin: "10px",
  },
  info: {
    display: "flex",
    flexFlow: "column",
    margin: "5px",
    "& > div": {
      margin: "20px",
    },
  },
  buttons: {},
  heading: {
    fontWeight: "bold",
  },
}));
