import { Button, Paper, Typography } from "@material-ui/core";
import React from "react";
import useRouter from "../../../hooks/UseRouter";
import { routes } from "../../../modules/Routing";
import { t } from "../../../services/LocaleService";
import ServiceListItem from "./ServiceListItem";

// TODO: any
interface Props {
  serviceList: any[];
}

const ServiceList: React.FC<Props> = ({ serviceList }: Props) => {
  const router = useRouter();

  const onNewServiceClick = () => {
    router.push(routes.new);
  };

  const onServiceClick = (id: string) => {
    router.push(routes.service + `/${id}`);
  };

  return (
    <Paper>
      <Typography variant="h5">{t("SERVICE_LIST.TITLE")}</Typography>
      <div style={headerStyle}>
        <Button variant="contained" onClick={onNewServiceClick}>
          {t("SERVICE_LIST.CREATE_BUTTON")}
        </Button>
      </div>
      {serviceList.map((item) => {
        return (
          <ServiceListItem
            key={item.id}
            item={item}
            onServiceClick={onServiceClick}
          />
        );
      })}
    </Paper>
  );
};

const headerStyle = {
  display: "flex",
  justifyContent: "space-between",
  padding: "10px",
};

export default ServiceList;
